  -webkit-overflow-scrolling: touch;
  border-radius: var(--border-radius-md);
  border: 1px solid var(--border-color);
}

.markdown-content .markdown-table {
  margin: 0;
  border-collapse: collapse;
  width: 100%;
  min-width: 300px;
  background-color: var(--background-color);
}

.markdown-content .markdown-thead {
  background-color: var(--quick-action-bg);
}

.markdown-content .markdown-tbody {
  background-color: transparent;
}

.markdown-content .markdown-tr {
  border-top: 1px solid var(--border-color);
}

.markdown-content .markdown-tr:hover {
  background-color: var(--quick-action-bg);
}

.markdown-content .markdown-th {
  padding: 0.6em 1em;
  font-weight: 600;
  text-align: left;
  border: 1px solid var(--border-color);
  background-color: var(--quick-action-bg);
}

.markdown-content .markdown-td {
  padding: 0.6em 1em;
  border: 1px solid var(--border-color);
  vertical-align: top;
}

/*  */
.markdown-content .markdown-strong {
  font-weight: 600;
  color: var(--text-color);
}

.markdown-content .markdown-em {
  font-style: italic;
  color: var(--text-color);
}

/*  */
.markdown-content .markdown-hr {
  height: 1px;
  padding: 0;
  margin: 1em 0;
  background-color: var(--border-color);
  border: 0;
  opacity: 0.8;
}

/*  */
.markdown-content .markdown-img {
  max-width: 100%;
  height: auto;
  border-radius: var(--border-radius-md);
  margin: 0.5em 0;
  box-shadow: var(--shadow-sm);
}

/*  */
.markdown-content .markdown-del {
  text-decoration: line-through;
  color: var(--text-secondary);
}

/*  */
.welcome-message {
  text-align: center;
  color: var(--text-secondary);
  padding: var(--spacing-lg);
  font-style: italic;
}

/*  */
.chat-messages {
  cursor: default;
  /*  */
  will-change: auto;
  contain: layout style;
  /*  */
  transform: translateZ(0);
  backface-visibility: hidden;
  perspective: 1000px;
}

/*  */
.chat-input {
  pointer-events: auto;
}

.chat-input-container {
  pointer-events: auto;
}

/*  */
.message-actions-container {
  display: flex;
  justify-content: flex-start;
  width: 100%;
  z-index: 1;
  gap: 8px;
  margin-top: 8px;
  /*  */
  will-change: auto;
  contain: layout;
}

.copy-button,
.share-button {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 32px;
  height: 32px;
  padding: 0;
  background-color: transparent;
  border: none;
  border-radius: 50%;
  color: var(--text-secondary);
  cursor: pointer;
  transition: all var(--transition-normal) ease;
  flex-shrink: 0;
  /*  */
  will-change: transform, background-color;
  /*  */
  transform: translateZ(0);
  backface-visibility: hidden;
}

.copy-button:hover,
.share-button:hover {
  background-color: var(--quick-action-bg);
  color: var(--primary-color);
  transform: translateY(-1px);
}

.copy-button:active,
.share-button:active {
  transform: translateY(0);
}

/*  */
.copy-button svg,
.share-button svg {
  width: 16px;
  height: 16px;
  stroke-width: 2;
}

/*  */
.copy-button,
.share-button {
  -webkit-tap-highlight-color: transparent;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  user-select: none;
