@media (max-width: 768px) {
  /*  */
  button,
  input[type="button"],
  input[type="submit"],
  input[type="reset"],
  input[type="image"],
  .button,
  .btn,
  .clickable,
  [role="button"],
  [onclick],
  [data-clickable] {
    -webkit-tap-highlight-color: transparent;
    -webkit-user-select: none;
    user-select: none;
    -webkit-touch-callout: none;
  }
  
  /*  */
  a,
  a *,
  [href] {
    -webkit-tap-highlight-color: transparent;
  }
  
  /*  */
  [tabindex],
  [contenteditable="true"],
  select,
  textarea,
  .interactive,
  .clickable-element {
    -webkit-tap-highlight-color: transparent;
    -webkit-user-select: none;
    user-select: none;
  }
  
  /*  */
  [onclick],
  [onmousedown],
  [onmouseup],
  [ontouchstart],
  [ontouchend] {
    -webkit-tap-highlight-color: transparent;
  }
  
  /* cursor: pointer */
  * {
    -webkit-tap-highlight-color: transparent;
  }
