.history-modal {
  background-color: var(--bg-color, #ffffff);
  border-radius: 8px;
  width: 90%;
  max-width: 500px;
  max-height: 80vh;
  display: flex;
  flex-direction: column;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.15);
}

.history-modal-header {
  padding: 16px 20px;
  border-bottom: 1px solid var(--border-color, #e0e0e0);
}

.history-modal-header h2 {
  margin: 0;
  font-size: 18px;
  font-weight: 600;
  color: var(--text-color, #333333);
  text-align: center;
}

/*  */
.history-search-container {
  padding: 12px 20px;
  border-bottom: 1px solid var(--border-color, #e0e0e0);
}

.history-search-input {
  width: 100%;
  padding: 8px 12px;
  border: 1px solid var(--border-color, #e0e0e0);
  border-radius: 4px;
  font-size: 14px;
  box-sizing: border-box;
}

.history-search-input:focus {
  outline: none;
  border-color: var(--primary-color, #007bff);
}

.history-modal-content {
  flex: 1;
  overflow-y: auto;
  padding: 16px 20px;
}

/*  */
.history-password-container {
  padding: 12px 20px;
  border-bottom: 1px solid var(--border-color, #e0e0e0);
  display: flex;
  gap: 8px;
}

.history-password-input {
  flex: 1;
  padding: 8px 12px;
  border: 1px solid var(--border-color, #e0e0e0);
  border-radius: 4px;
  font-size: 14px;
  box-sizing: border-box;
}

.history-password-input:focus {
  outline: none;
  border-color: var(--primary-color, #007bff);
}

.history-password-button {
  background-color: var(--primary-color, #007bff);
  color: white;
  border: none;
  padding: 8px 16px;
  border-radius: 4px;
  cursor: pointer;
  font-size: 14px;
  transition: background-color 0.2s;
}

.history-password-button:hover:not(:disabled) {
  background-color: var(--primary-hover-color, #0056b3);
}

.history-password-button:disabled {
  background-color: var(--disabled-color, #cccccc);
  cursor: not-allowed;
}

.history-empty {
  text-align: center;
  padding: 40px 0;
  color: var(--secondary-text-color, #888888);
}

.history-list {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.history-item {
  padding: 0;
  border: 1px solid var(--border-color, #e0e0e0);
  border-radius: 6px;
  margin-bottom: 12px;
  transition: all 0.2s;
}

.history-item:hover {
  border-color: var(--primary-color, #007bff);
}

.history-item-content {
  position: relative;
  padding: 12px 16px;
  cursor: pointer;
}

.history-item-content:hover {
  background-color: var(--hover-color, #f5f5f5);
}

.history-item-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 8px;
}

.history-item-name {
  font-weight: 600;
  color: var(--text-color, #333333);
  font-size: 16px;
  flex: 1;
}

.history-item-type {
  background-color: var(--primary-color, #007bff);
  color: white;
  font-size: 12px;
  padding: 2px 8px;
  border-radius: 12px;
}

/*  */
.history-item-actions {
  position: absolute;
  bottom: 8px;
  right: 8px;
  display: flex;
  gap: 4px;
}

.history-item-info {
  font-size: 14px;
  color: var(--text-color, #333333);
}

.history-edit-icon,
.history-delete-icon {
  width: 28px;
  height: 28px;
  border-radius: 50%;
  border: none;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 16px;
  transition: all 0.2s;
  background-color: transparent;
  opacity: 0.7;
}

.history-edit-icon {
  color: var(--primary-color, #007bff);
}

.history-edit-icon:hover {
  background-color: transparent;
  color: var(--primary-color, #007bff);
  opacity: 1;
}

.history-delete-icon {
  color: var(--danger-color, #dc3545);
}

.history-delete-icon:hover {
  background-color: transparent;
  color: var(--danger-color, #dc3545);
  opacity: 1;
}

/*  */
.history-editing-indicator {
  padding: 8px 16px;
  background-color: var(--primary-color-light, #e3f2fd);
  color: var(--primary-color, #007bff);
  font-size: 14px;
  text-align: center;
  border-radius: 4px;
  margin-bottom: 8px;
}

.history-modal-footer {
  padding: 12px 20px;
  border-top: 1px solid var(--border-color, #e0e0e0);
  display: flex;
  justify-content: flex-end;
}

.history-clear-button {
  background-color: transparent;
  border: 1px solid var(--danger-color, #dc3545);
  color: var(--danger-color, #dc3545);
  padding: 8px 16px;
  border-radius: 4px;
  cursor: pointer;
  font-size: 14px;
  transition: all 0.2s;
}

.history-clear-button:hover {
  background-color: var(--danger-color, #dc3545);
  color: white;
}

/*  -  */
.history-button {
  background: none !important;
  background-color: transparent !important;
  color: #000000 !important; /*  */
  border: none !important;
  padding: 0 !important;
  margin: var(--spacing-md) auto 0 !important; /*  */
